<template>
    <div class="c">
        <header class="h">
            <span class="left_f" @click="fan()">〈</span>
            <span class="pwd">密码登录</span>
        </header>

        <div class="input">
            <input type="text" v-model='name' placeholder="账号">
            <input class="pas" v-model="pwd" :type="type" placeholder="密码">
            <input type="text" v-model="yzm" placeholder="验证码">
        </div>

        <div class="ts">
            <p>温馨提示：未注册过的账号，登录时将自动注册</p>
            <p>注册过的用户可凭账号密码登录</p>
        </div>

        <div class="btn">
            <button :disabled='dis' @click="add()">登录</button>
        </div>

        <p class="cz">重置密码?</p>

        <Swiper @gb='gai'></Swiper>

        <p class="tu">
            <img :src="src" alt="">
        </p>
        <div class="yz" @click="qh()">
            <span>看不清</span><br>
            <span :style="{color:'#3190e8'}">换一张</span>
        </div>

        <p v-html="val" class="cuo"></p>

        <div class="sx" v-show="flag">
            <p>{{ sx.message }}</p>
            <p>{{ ys }}</p>
            <button @click="qr">确认</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
axios.defaults.withCredentials=true;//让ajax携带cookie

// import {ajaxcityP} from '../utils/api'
// import bus from '../plugins/bus'
import $ from 'jquery'
import Swiper from './Swiper'
export default {
    data() {
        return {
            name: '',
            src:'',
            dis:false,
            type:'password',
            val:'',
            pwd:'',
            yzm:'',
            sx:[],
            flag:false,
            ys:'',
            xid:'',
        };
    },
    created() {

    },
    mounted() {
        this.img()
    },
    methods: {
        setCookie(key,value,t){
            var oDate=new Date();
            oDate.setDate(oDate.getDate()+t);
            document.cookie=key+"="+value+"; expires="+oDate.toDateString();
        },
        fan(){
            this.$router.go(-1)
        },
        img(){
            // axios.post('http://elm.cangdu.org/v1/captchas').then((res) => {
            this.$http.post('http://elm.cangdu.org/v1/captchas').then((res) => {
                console.log(res)      
                this.src=res.data.code
            })
        },
        qh(){
            this.img()
        },
        gai(ind){
            console.log(ind)
            if(ind){
                this.type='text'
            }else{
                this.type='password'
            }
        },
        
        add(){

            let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
            if(!myreg.test(this.name)){
                this.val='请输入手机号或者用户名'
                return false
            }else{
                this.val=''
                
                this.setCookie("cap",this.yzm,2)
                this.setCookie("SID",100,2)

                // axios.post('/api/v2/login',{
                //     username:this.name,
                //     password:this.pwd,
                //     captcha_code:this.yzm
                //     }).then((res) => {
                    this.$http.post('/v2/login',{
                    username:this.name,
                    password:this.pwd,
                    captcha_code:this.yzm
                    }).then((res) => {
                    this.sx=res.data
                    console.log(this.sx)
                    console.log(this.sx.message)
                    if(this.sx.message=='验证码失效'){
                        this.flag=true
                    }
                    
                    this.xid=this.sx.id

                    console.log(this.sx)

                    if(this.name==this.sx.username){
                    // if(this.status==200){
                        let xid = JSON.parse(localStorage.getItem('xid'))

                        xid=this.xid
                        this.$router.push({
                            path:'/',
                            query:{
                                id:xid
                            }
                        })
                        console.log(this.xid)
                        localStorage.setItem('xid',JSON.stringify(xid))
                    }
                })
                return true
            }
        },
        // setCookie(key,value,t){
        //     var oDate=new Date();
        //     oDate.setDate(oDate.getDate()+t);
        //     document.cookie=key+"="+value+"; expires="+oDate.toDateString();
        // },
        qr(){
            this.flag=false
        }
    },
    watch:{

    },
    components: {
        Swiper
    }
};
</script>

<style lang='scss'>
.c{
    width: 4rem;
    height: 100%;
    background: #eee;
}
.h{
    width: 4rem;
    height: 0.5rem;
    line-height: 0.5rem;
    display: flex;
    background: #3190e8;
    color: white;
    .left_f{
        margin-left: 0.1rem;
        font-size: 0.2rem;
    }
    .pwd{
        font-size: 0.23rem;
        margin-left: 1.3rem;
    }
}
.input{
    width: 4rem;
    box-sizing: border-box;
    padding-top: 0.6rem;
    input{
        width: 4rem;
        height: 0.55rem;
        border: none;
        text-indent: 0.1rem;
        font-size: 0.18rem;
        border-bottom: 0.01rem solid #ddd;
    }
}
.ts{
    width: 4rem;
    height: 0.7rem;
    line-height: 0.23rem;
    margin-top: 0.17rem;
    color: red;
    text-indent: 0.1rem;
}
.btn{
    width: 4rem;
    height: 0.5rem;
    text-align: center;
    button{
        width: 3.9rem;
        height: 0.5rem;
        line-height: 0.5rem;
        background: #4CD964;
        border: 1px solid #4CD964;
        border-radius: 5px;
        color: white;
        font-size: 17px;
    }
}
.cz{
    width: 0.9rem;
    height: 0.3rem;
    margin-right: 0.1rem;
    color: #3B95E9;
    line-height: 0.3rem;
    text-align: center;
    float: right;
}
.tu{
    position: absolute;
    top: 1.87rem;
    right: 0.7rem;
}
.yz{
    position: absolute;
    top: 1.85rem;
    right: 0.12rem;
    font-size: 0.13rem;
}
.cuo{
    color: red;
    position: absolute;
    top: 0.77rem;
    right: 0.2rem;
}
.sx{
    width: 2.7rem;
    height: 2rem;
    background: white;
    position: absolute;
    top: 30%;
    left: 15%;
    line-height: 1.1rem;
    z-index: 1000;
    p{
        width: 4rem;
        height: 0.5rem;
        text-align: center;
        font-size: 0.22rem;
    }
    button{
        width: 4rem;
        height: 0.5rem;
        line-height: 0.5rem;
        margin-top: 0.56rem;
        border: 0.1rem solid #4CD964;
        background: #4CD964;
        color: white;
        border-radius: 0rem 0rem 0.05rem 0.05rem;
    }
}
</style>
