<template>
    <div>
        <router-view></router-view>
        <footer>
            <ul>
                <li @click="ly(item.id)" v-for="(item,key) in list" :key='key'>
                    <span :class="icon[item.id]"></span>
                    <p>{{ item.name }}</p>
                </li>
            </ul>
        </footer>
    </div>
</template>

<script>
export default {
    data() {
        return {
            icon:{
                "1":'icon-elemowaimai iconfont',
                "2":'icon-zhinan iconfont',
                "3":'icon-icon- iconfont',
                "4":'icon-wodedangxuan iconfont',
            },
            list:[
                {
                    name:'外卖',
                    id:1
                },
                {
                    name:'搜索',
                    id:2
                },
                {
                    name:'订单',
                    id:3
                },
                {
                    name:'我的',
                    id:4
                },
            ]
        };
    },
    created() {

    },
    mounted() {

    },
    methods: {
        ly(id){
            if(id==1){
                this.$router.push('/footer/list')
            }else if(id==2){
                this.$router.push('/footer/search')

            }else if(id==3){
                this.$router.push('/footer/dd')
            }else{
                this.$router.push('/footer/my')
            }
        }
    }
};
</script>

<style scoped lang='scss'>
footer{
    width: 100%;
    height: 0.65rem;
    background: #eee;
    position: fixed;
    bottom: 0px;
    ul{
        width: 100%;
        height: 0.6rem;
        display: flex;
        padding: 0.1rem 0rem;
        li{
            flex: 1;
            text-align: center;
            span{
                font-size: 0.22rem;
            }
            p{
                font-size: 0.13rem;
            }
        }
    }
}
</style>
