<template>
    <div class="b">
    <Header :id='id'></Header>
        <div class="box">
            <div class="cont">
                <div class="city_tip" id="top">
                    <span>当前定位的城市：</span>
                    <span>定位不准时，请在城市列表中选择</span>
                </div>
                <div class="guess_city" v-for="(item,index) in loc" :key='index'>
                    <span class="arrow_left">{{ item.name }}</span>
                    <span class="arrow_right">〉</span>
                </div>

                <!-- <div class="grid">
                    <p>热门城市</p>
                    <van-grid class="van-grid">
                        <van-grid-item class="item" v-for="(item,key) in hot" :key='key' :text="item.name" />
                    </van-grid>
                </div> -->
                <div class="grid">
                    <p>热门城市</p>
                    <ol class="van-grid">
                        <li @click="xq(item.id,item.name)" v-for="(item,key) in hot" :key='key'>
                            <span>{{ item.name }}</span>
                        </li>
                    </ol>
                </div>

                <!-- <div class="city_a">
                    <ul class="ul">
                        <li v-for="(item,key) in obj" :key='key'>
                            <a class='md' :href="'#'+k" v-for="(i,k) in item" :key='k'>
                                {{ k }}
                            </a>
                        </li>
                    </ul>
                </div> -->

                <div class="title">
                    <ul>
                        <li v-for="(item,key) in obj" :key='key'>
                            <p :id="k" class="city_title" v-for="(i,k) in item" :key='k'>
                                {{ k }}
                                <ol>
                                    <li v-for="(m,index) in i" :key='index' @click="cs(m.name)">
                                        {{ m.name }}
                                    </li>
                                </ol>
                            </p>
                        </li>
                    </ul>
                </div>
                
            </div>
        </div>
        <div class="city_l">
            <ul class="uu">
                <li v-for="(item,key) in obj" :key='key'>
                    <a class='md' :href="'#'+k" v-for="(i,k) in item" :key='k'>
                        {{ k }}
                    </a>
                </li>
            </ul>
        </div>
        <a href="#top" class="top" v-show="flag" @click="top">∧</a>
    </div>
</template>

<script>
import {ajaxcityG} from '../utils/api'
import Header from './Header'
import IScroll from '../plugins/iscroll-probe'
export default {
    data() {
        return {
            loc:[],
            iscroll:'',
            hot:[],
            list:{},
            flag:false,
            obj:[],
            id:'',
        };
    },
    created() {

    },
    async mounted() {
        let { data } = await this.$Axios.get('http://elm.cangdu.org/v1/cities?type=guess')
        // let { data } = await ajaxcityG('guess')
        console.log(data)
        this.loc.push(data)
        // console.log(this.loc)

        let { data:res } = await this.$Axios.get('http://elm.cangdu.org/v1/cities?type=hot')
        this.hot = res
        console.log(this.hot)

        let { data:msg } = await this.$Axios.get('http://elm.cangdu.org/v1/cities?type=group')
        let px=Object.keys(msg)
        px.sort()
        // console.log(px)

        for(let i in px){
            this.list[px[i]]=msg[px[i]]
        }

        this.obj.push(this.list)

        // console.log(this.obj)
        // console.log(this.list)

        this.iscroll = new IScroll('.box',{})
        this.$nextTick(()=>{
            this.iscroll.refresh()
        })

        this.iscroll.on('scrollEnd',()=>{
            console.log(this.iscroll)
            if((this.iscroll.y*-1)>=371){
                this.flag=true
            }else{
                this.flag=false
            }
        })

        this.id = localStorage.getItem('xid')
        console.log(this.id)
        
    },
    methods: {
        top(){
            this.iscroll.scrollTo(0,0,1000)
        },
        cs(val){
            console.log(val)
            this.$router.push({
                path:'/detail',
                query:{
                    val:val
                }
            })
        },
        xq(id,name){
            this.$http.get(`/v1/pois?type=search$city_id=${id}&keyword=${name}`).then((data)=>{
                console.log(data)
                this.$router.push({
                   path:'/xq',
                   query:{
                       data:data,
                       name:name,
                       id:id,
                   } 
                })
            })
            // this.$http.get(`http://elm.cangdu.org/v1/user?user_id=${id}`).then((data) => {
            //     console.log(data)
            //     this.$router.push({
            //        path:'/xq',
            //        query:{
            //            data:data,
            //            name:name
            //        } 
            //     })
            // })
        }
    },
    components: {
        Header
    }
};
</script>

<style lang='scss'>
.city_l{
    height: 4rem;
    position: fixed;
    right: 0;
    top: 1.8rem;
    a{
        display: block;
        margin-top: 0.07rem;
        color: black;
        // color: #3190e8;
    }
}
.b{
    width: 4rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    .box{
        flex: 1;
        overflow: hidden;
        margin-top: 0.5rem;
        .cont{
            min-height: 101%;
            background: #eee;
        }
    }
}
.top{
    width: 0.55rem;
    height: 0.55rem;
    text-align: center;
    line-height: 0.53rem;
    border-radius: 0.5rem;
    background: #ddd;
    position: fixed;
    bottom: 0.31rem;
    right: 0.17rem;
    z-index: 1000;
    font-size: 0.30rem;
    color: white;
}
.ul{
    width: 4rem;
    background: white;
    margin-top: 0.10rem;
    li{
        width: 4rem;
        display: flex;
        flex-wrap: wrap;
        margin-left: 0.13rem;
        a{
            width: 0.4rem;
            height: 0.4rem;
            margin-top: 0.2rem;
            color: #3190e8;
        }
    }
}
ul,ol{
    list-style: none;
}
.city_tip{
    width: 4rem;
    height: 0.4rem;
    background: white;
    border-bottom: 0.01rem solid #ddd;
    font-size: 0.14rem;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0.1rem 0.1rem;
}
.guess_city{
    width: 4rem;
    height: 0.4rem;
    line-height: 0.4rem;
    background: white;
    box-sizing: border-box;
    border-bottom: 0.01rem solid #ddd;
    padding: 0rem 0.1rem;
}
.arrow_left{
    font-size: 0.21rem;
    color: #3190e8;
}
.arrow_right{
    float: right;
}
.grid{
    width: 4rem;
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
    border: 0.01rem solid #ddd;
    border-left: none;
    border-right: none;
    p{
        width: 4rem;
        height: 0.4rem;
        line-height: 0.4rem;
        margin-bottom: 0.1rem;
        font-size: 0.14rem;
        text-indent: 0.13rem;
        background: white;
    }
    // .van-grid{
    //     margin-top: -0.14rem;
    //     border-bottom: 0.02rem solid #E8E8E8;
    //     .item{
    //         border: 0.01rem solid #E8E8E8;
    //         border-left: none;
    //         border-bottom: none;
    //     }
    // }
    .van-grid{
        width: 4rem;
        display: flex;
        flex-wrap: wrap;
        background: white;
        margin-top: -0.14rem;
        li{
            width: 0.99rem;
            height: 0.5rem;
            text-align: center;
            line-height: 0.5rem;
            font-size: 0.13rem;
            border: 0.01rem solid #ddd;
            border-left: none;
            border-bottom: none;
            span{
                color: #3190e8;
            }
        }
    }
}
.title{
    width: 4rem;
    ul{
        width: 4rem;
        li{
            width: 4rem;
            display: flex;
            flex-direction: column;
            p{
                width: 4rem;
                line-height: 0.3rem;
                background: white;
            }
            ol{
                width: 4rem;
                height: 100%;
                display: flex;
                flex-wrap: wrap;
                margin-top: 0.05rem;
                li{
                    box-sizing: border-box;
                    font-size: 0.14rem;
                    width: 1.33rem;
                    height: 0.5rem;
                    text-align: center;
                    line-height: 0.5rem;
                    border: 0.01rem solid #E8E8E8;
                    overflow: hidden;
                    
                }
            }
        }
    }
}
</style>
